}

button {
	font-family:'M PLUS Rounded 1c', sans-serif;
	margin: 0px 0px 10px 10px;
	color: #496D89;
	color: var(--blue);
	position: relative;
	overflow: hidden;
	z-index: 1;
	transition: color 100ms ease-in-out;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background-color: transparent;
	border: 2px solid #496D89;
	border: 2px solid var(--blue);
	cursor: pointer;
	font-size: 1rem;
	font-weight: 700;
	line-height: 1;
	padding: 1rem 2rem;
	text-decoration: none;
	text-align: center;
	text-transform: uppercase;
	}
	
button::after {
	  content: '';
	  position: absolute;
	  display: block;
	  top: 0;
	  left: 50%;
	  transform: translateX(-50%);
	  width: 0;
	  height: 100%;
	  background: #496D89;
	  background: var(--blue);
	  z-index: -1;
	  transition: width 150ms ease-in-out;
	}
	
button:hover {
	  color: #fff;}
	  
button:hover::after {
		width: 110%;
	}
   

a { text-decoration:none; 
	color: inherit;}


