  }

/* Nav Bar  */
.navbar {

  width: 100%; 
  position:fixed;
  display:flex;   
  background-color: rgba(0,0,0,0.7);
  color:white; 
  top: 0;
  z-index: 100;
  font-size: 1.2rem;
  flex: 1 1; 
  }
  
  nav ul {
  display: flex;
  list-style-type: none;
  flex:auto;
  }
  
  nav ul li {
  flex: auto; }
  
  nav li {
    padding: 0.3rem 0rem; 
    color:white;
    }
  
  li:hover, .is-active{
      padding: 0.3rem 0rem; 
      background-color:white;
      color:black;
      }
  
  
  /* Banner */
  
